import popupMenu from "@/components/popupMenu.vue"
import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue";
let _this,timer;
export default {
	components:{
		popupMenu,
		uniLoadMore
	},
	data() {
		return {
			loadingType:'more',
			content:'',
			isShowLabel: false,
			dataIndex: 1,
			form:{
				counter:''
			},
			secondPass:'',
			current: 0,
			items:[],
			pageNum: 1,
			pageSize: 20,
		
		};
	},
	onReachBottom:function(){
		 if (timer != null) {
		            clearTimeout(timer);
		        }
		   timer = setTimeout(function() {
		        _this.fetchList();
		        }, 1000);
	},
	onLoad:function(){
		_this = this
		this.fetchList()
	},
	methods:{
		async fetchList(){
			if (this.loadingType == ''){
				return
			}
			let data ={
				userCode: '',	//用户编码。查看本用户的时候，随便传个值。否则传空字符串
				pageNum: this.pageNum,
				pageSize: this.pageSize
			}
			if (this.dataIndex ==1){
				const res = await this.$http.get('/udeal/buy_list',{data: data})
				if (res.data.data.isLastPage){
					this.loadingType = ''
				}else {
					this.pageSize += 20
				}
				this.items = res.data.data.list
				console.log(res)
			}else {
				const res = await this.$http.get('/udeal/sell_list',{data: data})
				if (res.data.data.isLastPage){
					this.loadingType = ''
				}else {
					this.pageSize += 20
				}
				this.items = res.data.data.list
				console.log(res)
			}
			
		},
		showDialog(){
			this.$refs.dialog.show()
		},
		setShowLabel:function(){
			this.isShowLabel = !this.isShowLabel
			this.showDialog()
		},
		selectLabel:function(e){
			this.dataIndex = e.target.dataset.index
			this.pageSize = 20
			this.loadingType = 'more'
			this.fetchList()
			
		},
		async submit(){
			
		},
		confirmInput:function(e){
			console.log(e)
			if (e==1){
				uni.navigateTo({
					url: './orderRecord'
					
				});
			}else if (e ==2){
				uni.navigateTo({
					url: './orderList'
					
				});
			}else if (e==3){
				uni.navigateTo({
					url: './buySellUsdf',
					
				});
			}
		},
		// radioChange: function(evt) {
		//             for (let i = 0; i < this.items.length; i++) {
		//                 if (this.items[i].value === evt.target.value) {
		//                     this.current = i;
		//                     break;
		//                 }
		//             }
		// 			console.log(this.current)
		// },
	}
	
}
